@import "./base.scss";




.landing{
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: url("../shapes/tumblr.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  color: $white-color;
  overflow: hidden;

.header{
  position: absolute;
  z-index: 2;
  top: 1rem;
  left: 50%;
  transform: translateX(-50%);
  &__wrap {
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 60vw;

  }  &__left {
       display: flex;
       align-items: center;
       gap: 2rem;
     & li{
       font-size: 18px;
       font-weight: 200;
     }
       }
  @media (max-width: 1200px) {
    &__wrap{
      width: 100vw;
    }
  }
  @media (max-width: 650px) {
    &__sm{
 display: none;
    }
  }
  @media (max-width: 370px) {
      &{
        left: 36%;
      }
    &__wrap {
    gap: 20%;
         }

    .coolLink[data-text='Home']{
      display: none;
    }
  }
}
  .socials{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 10rem;
    display: flex;
    align-items: center;
    gap: 1rem;
    z-index: 2;

  }
  .scrollDown {
    position: absolute;
    bottom: 3rem;
    z-index: 2;
    left: 50%;
    transform: translateX(-50%);
    &__wheel{
      height: 42px;
      width: 24px;
      border-radius: 10px;
      border: 2px solid $white-color;
      &--inner{
        position: absolute;
        left: 10px;
        margin-top: 5px;
        width: 4px;
        height: 4px;
        background: $white-color;
        border-radius: 50%;
        animation: movingWheel 0.7s linear infinite ;
        @keyframes movingWheel {
          0%{
            top: 1px;
          }
          25%{
            top: 2px;
          }
          50%{
            top: 3px;
          }
          75%{
            top: 2px;
          }
          100%{
            top: 1px;
          }
        }
      }
    }
  .scrollDown__arrows {
    display: flex;
    flex-direction: column;
    align-items: center;
    .scrollDown__arrow{
      width: 15px;
      height: 15px;
      border-right:2px solid $white-color ;
      border-bottom:2px solid $white-color ;
      transform: rotate(45deg);
      animation: scrollAnim 1s infinite;
      animation-direction: alternate;
      &:nth-child(1){
        animation-delay: 0.1s;
      }
      &:nth-child(2) {
        animation-delay: 0.2s;

      }
      &:nth-child(3){
        animation-delay: 0.3s;
      }
      @keyframes scrollAnim {
        0%{
          opacity: 0;
        }25%{
        opacity: 0.6;
                 }
        50%{
        opacity: 0.8;
        }
        100%{
          opacity: 1;
        }
      }
    }
  }
  } .intro{
    position: absolute;
    top: 20%;
    left: 1rem;
    width: 100%;
    height: 60%;
    z-index: 2;
    color: $white-color;

  & h1{
    width: 50%;
    padding-left: 2rem;
    padding-top: 1rem;
    font-family: 'MADE TheArtist Script', sans-serif;
    font-weight: 500;
    font-size: 50px;

  }
  @media (min-width: 600px) {
    & h1{
      font-size: 80px;

    }
  }
  @media (min-width: 1200px) {
    & h1{
      font-size: 90px;

    }
  }
  }
}

