/* STYLES */
*{
margin: 0;
  box-sizing: border-box;
&:after,&:before{
  box-sizing: border-box;
}
}

html, body{
  overflow-x: hidden;
}
body {
  background-color: cadetblue;
  font-family: "Raleway", sans-serif;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;

}
button ,
input,
textarea,
select {
  font: inherit;
}
p,h1,h2,h3,h4,h5,h6{
  overflow-wrap: break-word;
}

img,video,canvas,svg{
  max-width: 100%;
}
a{
  text-decoration: none;
  color: inherit;
}
ul{
list-style: none;
}





/*  COLOR--VARIABLES */
$green-color : #6cff8d;
$dark-green-color : #6cff8c7a;
$dark-blue: #0b134f;
$white-color: #ffffff;
$red-color : #ff0000;
$blue-color : #2196f3;
$bg-landing : #111;
$yellow-color: #ffea5c;
$darkish-blue:#060b11;
$box-shadow-blue1:rgba(31,38,135,0.27);
$box-shadow-blue2:rgb(31,38,135,0.67);

/*  Predefined classes */

.section{
  padding: 1rem;
  overflow: hidden;
  min-height: 100vh;
  padding-top: 5rem;
  
@media (min-width:650px ) {
  padding: 5rem;
}
}






